import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanHealthUtil} from "@util/vsan-health-util";

@Component({
   templateUrl: "historical-instance.html",
   styleUrls: ["historical-instance.scss"],
   selector: "vsan-historical-instance",
})
export  class HistoricalInstanceComponent {

   VsanHealthStatus: typeof VsanHealthStatus = VsanHealthStatus;

   @Input()
   isActive: boolean;

   @Input()
   status: VsanHealthStatus;

   @Input()
   timestamp: number;

   @Input()
   showTitle: boolean;

   @Output()
   onClick: EventEmitter<number> = new EventEmitter<number>();

   formatTitle(timestamp: number): string {
      return VsanHealthUtil.formatInstanceTitleTimestamp(timestamp);
   }

   formatInstanceDetails(timestamp: number): string {
      return VsanHealthUtil.formatInstanceTimestamp(timestamp);
   }
}