import {Injectable} from "@angular/core";
import {Logger} from "@util/logger";

/**
 * Introduces h5 client telemetry service. The telemetry base may be accessed at {@link http://url/q7n2}.
 * NOTE: The replication of logged event to the base may take up to 24 hours.
 */
@Injectable()
export class TelemetryService {

   private readonly EVENT_TYPE: string = "trackEvent";

   // h5 client initializes global variable, which is used to handle telemetry.
   private h5Window: Window = window.parent;
   private paq: any;

   constructor() {
      this.paq = (this.h5Window as PaqAware)._paq;
      if (!this.paq) {
         Logger.error("Unable to initialize telemetry service");
      }
   }

   logAction(eventCategory: EventCategory, eventAction: string, eventName: string, value: any) {
      if (!this.paq) {
         Logger.error("Telemetry service is not initialized. Entry is not collected.");
         return;
      }
      this.paq.push([this.EVENT_TYPE, EventCategory[eventCategory], eventAction, eventName, value]);
   }
}

export interface PaqAware extends Window {
   // h5 client initializes this global variable, which is used to handle telemetry. Pushing a value to will be logged
   // into the telemetry database.
   _paq: any;
}

/**
 * Category for the telemetry entry. To ease the search in the base, add "vsan_" prefix to all new categories.
 */
export enum EventCategory {
   VSAN_HEALTH,
}