import {VsanTestInstanceDetails} from "@generated/vsan-test-instance-details";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * A period of time. Each period should know its parent period, if it contains child periods + how to
 * build them, if it contains test instances, how to build its own title.
 * This class implements an element of a tree. Only the leafs hold instances.
 */
export abstract class BasePeriod {
   private readonly SHORT_DATE_OPTIONS: Intl.DateTimeFormatOptions = {
      month: "short",
      day: "numeric",
      hour12: false,
   };

   constructor(public id?: number) {
   }

   /**
    * The status of the period. It is summarized status of all propagated test instances.
    */
   status: VsanHealthStatus = VsanHealthStatus.green;

   /**
    * The count of the test instances which are propagated through the period.
    */
   propagatedInstancesCount: number = 0;

   /**
    * The count of the test instances with red status which are propagated through the period.
    */
   propagatedErrorInstancesCount: number = 0;

   /**
    * The count of the test instances with yellow status which are propagated through the period.
    */
   propagatedWarningInstancesCount: number = 0;

   /**
    * The count of the test instances with info status which are propagated through the period.
    */
   propagatedInfoInstancesCount: number = 0;

   /**
    * Reference to the parent of the period
    */
   parentPeriod: BasePeriod;

   /**
    * References to children periods
    */
   childPeriods: BasePeriod[] = [];

   /**
    * Propagated test instances. Only leafs may have test instances.
    */
   testInstances: VsanTestInstanceDetails[] = [];

   /**
    * Returns a title of the period
    */
   abstract generateTitle(): string;

   /**
    * Returns the id of its child element where a test instance with given timestamp should be propagated.
    */
   protected abstract getChildId(timestamp: number): number;

   /**
    * Creates a new child element.
    */
   protected abstract createChild(id: number): BasePeriod;

   /**
    * Returns if the period is a leaf. If element is a leaf, it has no child periods. Only leafs have test instaces.
    */
   protected abstract isLeaf(): boolean;

   /**
    * Propagates the given test instace to test instances (if the period is a leaf) or to a child period. At the end of
    * loop the instance should be added to a leaf period.
    */
   propagateTestInstance(testInstance: VsanTestInstanceDetails): void {
      this.updatePropagatedTestInstancesInfo(testInstance);
      if (!this.isLeaf()) {
         const child: BasePeriod = this.getChild(testInstance);
         child.propagateTestInstance(testInstance);
      }

      if (this.isLeaf()) {
         this.testInstances.push(testInstance);
      }
   }

   /**
    * Checks if the period has a test instance in a given timestamp.
    */
   hasTestInstance(timestamp: number): boolean {
      if (VsanUiUtils.isEmpty(this.testInstances)) {
         return false;
      }
      return this.testInstances[0].timestamp <= timestamp &&
            this.testInstances[this.testInstances.length - 1].timestamp >= timestamp;
   }

   private updatePropagatedTestInstancesInfo(testInstance: VsanTestInstanceDetails): void {
      this.propagatedInstancesCount++;

      // update status information
      switch (testInstance.status) {
         case VsanHealthStatus.red:
            this.propagatedErrorInstancesCount++;
            this.status = VsanHealthStatus.red;
            break;
         case VsanHealthStatus.yellow:
            this.propagatedWarningInstancesCount++;
            if (this.status !== VsanHealthStatus.red) {
               this.status = VsanHealthStatus.yellow;
            }
            break;
         case VsanHealthStatus.info:
            this.propagatedInfoInstancesCount++;
            if (this.status !== VsanHealthStatus.red && this.status !== VsanHealthStatus.yellow) {
               this.status = VsanHealthStatus.info;
            }
            break;
      }
   }

   private getChild(testInstance: VsanTestInstanceDetails): BasePeriod {
      const childId: number = this.getChildId(testInstance.timestamp);
      let child: BasePeriod = this.childPeriods.find(value => value.id === childId);
      if (!child) {
         child = this.createChild(childId);
         child.parentPeriod = this;
         this.childPeriods.push(child);
      }
      return child;
   }

   /**
    * Returns a formatted date including only month and day of month
    */
   protected formatDate(date: Date): string {
      return VsanDateTimeUtils.formatDate(date, this.SHORT_DATE_OPTIONS);
   }
}