import {ChangeDetectorRef, Component, EventEmitter, Input, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthMutationProvider} from "@generated/vsan-health-mutation-provider";
import {VsanTestData} from "@generated/vsan-test-data";
import {TaskMonitorService} from "@service/task-monitor-service";

@Component({
   selector: "vsan-proactive-rebalance-action",
   templateUrl: "proactive-rebalance-action.html",
})
export class ProactiveRebalanceActionComponent {

   errorMessage: string;
   showConfirmationDlog: boolean;
   isRunning: boolean;
   isStopping: boolean;
   private _data: VsanTestData;

   @Input()
   set testData(val: VsanTestData) {
      this._data = val;
      /**
       * The actions is a 2-element array, start/stop balance action
       */
      this.isRunning = val.actions && val.actions.length > 1 ? val.actions[1].enabled : false;
   }

   get testData(): VsanTestData {
      return this._data;
   }

   @Output()
   private onStopDiskRebalance: EventEmitter<boolean> = new EventEmitter();

   @Output()
   private onFinishDiskRebalance: EventEmitter<boolean> = new EventEmitter();

   @Input()
   isDisabled: boolean = false;

   constructor(private healthMutationProvider: VsanHealthMutationProvider,
         private taskSvc: TaskMonitorService,
         private changeDetector: ChangeDetectorRef) {
   }

   startDiskRebalance = (): void => {
      this.healthMutationProvider.rebalanceCluster(ManagedObject.contextObject)
            .then((task: ManagedObject) => {
               this.taskSvc.getAwaitResult(task).then(() => {
                  this.isRunning = false;
                  this.onFinishDiskRebalance.emit(true);
               });
            })
            .catch(() => {
               this.isRunning = false;
               this.errorMessage = VsanUiUtils.getString("vsan.monitor.cluster.disk.rebalance.start.error");
            });
      this.isRunning = true;
      this.showConfirmationDlog = false;
      this.changeDetector.detectChanges();
   };

   stopDiskRebalance = (): void => {
      this.isStopping = true;
      this.healthMutationProvider.stopRebalanceCluster(ManagedObject.contextObject)
            .then((task: ManagedObject) => {
               this.taskSvc.getAwaitResult(task).then(() => {
                  this.isRunning = false;
                  this.isStopping = false;
                  this.onStopDiskRebalance.emit(true);
               });
            })
            .catch(() => {
               this.isStopping = false;
               this.errorMessage = VsanUiUtils.getString("vsan.monitor.cluster.disk.rebalance.stop.error");
            });
   };

   get startActionDisabled(): boolean {
      /**
       * If the init value is disabled, then we should disable the start proactive button
       */
      return !this.testData || !this.testData.actions[0].enabled;
   }

   get stopActionDisabled(): boolean {
      /**
       * disable the stop proactive button when:
       * 1. the stop proactive button is disabled from the backend data
       * 2. no running disk rebalance task
       * 3. the rebalance task is stopping
       */
      return ((!this.testData || !this.testData.actions[1].enabled)
            && !this.isRunning)
            || this.isStopping;
   }
}