import {BasePeriod} from "./base-period";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {WeekPeriod} from "./week-period";

/**
 * The root of the tree of periods. It should be not included as a child in any other period.
 */
export class LargestPeriod extends BasePeriod {

   generateTitle(): string {
      return "";
   }

   protected createChild(id: number): BasePeriod {
      return new WeekPeriod(id);
   }

   protected getChildId(timestamp: any): number {
      return VsanDateTimeUtils.getFirstDayOfWeek(timestamp).getTime();
   }

   protected isLeaf(): boolean {
      return false;
   }

}