import {Directive, ElementRef, EmbeddedViewRef, Input, OnInit, TemplateRef, ViewContainerRef} from "@angular/core";
import {Logger} from "@util/logger";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Applied on container it gives option to hide / show its content.
 *    - The trigger DOM elements for the actions are provided as parameters (i.e., TemplateRef).
 *    - The initial state of container is defined by the value of {@code state}.
 */
@Directive({
   selector: "[vsan-show-hide]",
   providers: [ReferenceWatcher],
})
export class ShowHideDirective implements OnInit {

   private readonly DISPLAY_HIDE: string = "none";
   private readonly DISPLAY_SHOW: string = "flex";

   _state: ShowHideState = ShowHideState.HIDDEN;

   @Input("vsan-show-hide")
   set state(value: ShowHideState) {
      if (value == null || value === this._state) {
         return;
      }
      this._state = value;
      this.refWatcher.setTimeout(() => this.onStateChanged(), 0);
   }

   get state(): ShowHideState {
      return this._state;
   }

   @Input()
   showTriggerElement: TemplateRef<HTMLElement>;

   @Input()
   hideTriggerElement: TemplateRef<HTMLElement>;

   constructor(private viewContainerRef: ViewContainerRef,  private el: ElementRef<HTMLElement>,
         private refWatcher: ReferenceWatcher) {}

   ngOnInit(): void {
      this.onStateChanged();
   }

   private onStateChanged() {
      // schedule setup of the element
      switch (this.state) {
         case ShowHideState.DISABLED:
            this.disableFunctionality();
            break;
         case ShowHideState.SHOWN:
            this.showElement();
            break;
         case ShowHideState.HIDDEN:
            this.hideElement();
            break;
      }
   }

   private disableFunctionality() {
      this.viewContainerRef.clear();
      this.el.nativeElement.style.display = this.DISPLAY_SHOW;
   }

   showElement: ClickFunction = ($event) => {
      // if click function is called because mouse click parameter will be set, otherwise will be null
      // set focus only if user clicks the trigger element, do not at inilialization
      this.prepareTrigger(this.hideTriggerElement, this.hideElement, !!$event);
      this.el.nativeElement.style.display = this.DISPLAY_SHOW;
   };

   hideElement: ClickFunction = ($event) => {
      // if click function is called because mouse click parameter will be set, otherwise will be null
      // set focus only if user clicks the trigger element, do not at inilialization
      this.prepareTrigger(this.showTriggerElement, this.showElement, !!$event);
      this.el.nativeElement.style.display = this.DISPLAY_HIDE;
   };

   private prepareTrigger(triggerElement: TemplateRef<HTMLElement>, clickFunction: ClickFunction,
         setFocus: boolean): void {
      this.viewContainerRef.clear();
      const triggerElementViewRef: EmbeddedViewRef<HTMLElement> = this.viewContainerRef.createEmbeddedView(triggerElement);
      const triggerNode: HTMLElement = this.findTriggerDomNode(triggerElementViewRef);
      if (!triggerNode) {
         Logger.error("Not able to find trigger DOM element in showTriggerElement or hideTriggerElement inputs.");
         return;
      }
      triggerNode.style.cursor = "pointer";
      triggerNode.onclick = clickFunction;

      if (setFocus) {
         const focusableElement: HTMLElement = this.getFirstFocusableElement(triggerNode);
         if (focusableElement) {
            focusableElement.focus();
         }
      }
   }

   /**
    * Finds the first node element from the provided template
    */
   private findTriggerDomNode(triggerElementViewRef: EmbeddedViewRef<HTMLElement>): HTMLElement {
      if (!triggerElementViewRef || VsanUiUtils.isEmpty(triggerElementViewRef.rootNodes)) {
         return null;
      }
      return triggerElementViewRef.rootNodes.find(this.isElementNode);
   }

   private isElementNode(node: Node): boolean {
      return node && node.nodeType === Node.ELEMENT_NODE;
   }

   private getFirstFocusableElement(element: HTMLElement): HTMLElement {
      if (this.isElementFocusable(element)) {
         return element;
      }
      if (element.children) {
         // tslint:disable-next-line:prefer-for-of
         for (let i: number = 0; i < element.children.length; i++) {
            const child: HTMLElement = this.getFirstFocusableElement(element.children.item(i) as HTMLElement);
            if (this.isElementFocusable(child)) {
               return child;
            }
         }
      }
      return null;
   }

   private isElementFocusable(element: HTMLElement): boolean {
      return element.tabIndex > -1;
   }
}

// Specific type which satisfies HTMLElement#onclick expectation
type ClickFunction = ($event?) => any;

export enum ShowHideState {
   SHOWN, HIDDEN, DISABLED,
}