import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanHealthStatus} from "@generated/vsan-health-status";

@Component({
   templateUrl: "historical-period.html",
   styleUrls: ["historical-period.scss"],
   selector: "vsan-historical-period",
})
export class HistoricalPeriodComponent {

   VsanHealthStatus = VsanHealthStatus;

   @Input()
   id: string;

   @Input()
   status: VsanHealthStatus;

   @Input()
   title: string;

   @Input()
   details: string;

   @Input()
   tooltip: string;

   @Output()
   onClick: EventEmitter<void> = new EventEmitter();

   getItemClass(): string {
      switch (this.status) {
         case VsanHealthStatus.green: return "green-item";
         case VsanHealthStatus.info: return "blue-item";
         case VsanHealthStatus.yellow: return "yellow-item";
         case VsanHealthStatus.red: return "red-item";
         default: return "grey-item";
      }
   }
}