import {BasePeriod} from "./base-period";
import {DayPeriod} from "./day-period";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";

/**
 * Represents one week. It may be only a part of the week, depending of children days.
 */
export class WeekPeriod extends BasePeriod {

   protected createChild(id: number): BasePeriod {
      return new DayPeriod(id);
   }

   generateTitle(): string {
      if (!this.childPeriods || !this.childPeriods.length) {
         return "";
      }

      const firstDayTitle: string = this.childPeriods[0].generateTitle();
      const lastDayTitle: string = this.childPeriods[this.childPeriods.length - 1].generateTitle();
      return firstDayTitle === lastDayTitle ? firstDayTitle : `${firstDayTitle} - ${lastDayTitle}`;
   }

   protected getChildId(timestamp: number): number {
      return VsanDateTimeUtils.getStartOfTheDay(timestamp).getTime();
   }

   protected isLeaf(): boolean {
      return false;
   }

}