import {Component, EventEmitter, Input, OnInit, Output, QueryList, ViewChildren,} from "@angular/core";
import {ClrTabLink} from "@clr/angular";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {HealthAction} from "@generated/health-action";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanHealthMutationProvider} from "@generated/vsan-health-mutation-provider";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {VsanTestData} from "@generated/vsan-test-data";
import {VsanTestInstanceDetails} from "@generated/vsan-test-instance-details";
import {VsanTestTable} from "@generated/vsan-test-table";
import {CapacityManagementContext} from "@navigation/context/capacity-management-context";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {ServicesContext} from "@service/navigation/context/services-context";
import {NavigationService, PlatformExtension} from "@service/navigation/navigation.service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {EventCategory, TelemetryService} from "@service/telemetry.service";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../../../vsan-routes";
import {SilentHealthActionContext} from "../../action/mark-silent-health-action.component";
import {RestoreHealthActionContext} from "../../action/model/restore-health-action-context.data";
import {RestoreHealthSettings} from "../../action/model/restore-health-settings.data";
import {RemediateActions} from "../../action/remediate-action-constants";
import {RemediateClusterConfigContextData} from "../../action/remediate-cluster-config-context.data";
import {HealthRetestEventData} from "../../model/health-retest-event.data";
import {HealthSilentChangeData} from "../../model/health-silent-change.data";
import {HealthTestNavParams} from "../../model/health-test-nav-params";
import {HealthLazyDataLoadingUtil} from "../health-lazy-data-loading-util";

@Component({
   templateUrl: "details-card.html",
   styleUrls: ["details-card.scss"],
   selector: "vsan-health-details-card",
   providers: [ReferenceWatcher],
})
export class DetailsCardComponent implements OnInit {
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanHealthUtil: typeof VsanHealthUtil = VsanHealthUtil;
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   readonly GOTO_HELP_EVENT: string = "goto_help";
   readonly EVENT_PARAM_NAME: string = "helpId";

   readonly TEST_ID_SKYLINE: string = "com.vmware.vsan.health.test.advisor";
   private readonly HELP_ID_HCL_UPTODATE: string = "com.vmware.vsan.health.test.hcldbuptodate";
   private readonly HELP_ID_RELEASE_CATALOG_UPTODATE: string = "com.vmware.vsan.health.test.releasecataloguptodate";
   private readonly HELP_ID_DISK_BALANCE: string = "com.vmware.vsan.health.test.diskbalance";
   private readonly HELP_ID_DISK_SPACE: string = "com.vmware.vsan.health.test.diskspace";
   private readonly HELP_ID_SELECT_VCG_DEVICE_MODELS: string = "com.vmware.vsan.health.test.nvmeidentifiable";

   @ViewChildren(ClrTabLink)
   private tabs: QueryList<ClrTabLink>;

   @Output()
   onRetest: EventEmitter<HealthRetestEventData> = new EventEmitter<HealthRetestEventData>();

   @Output()
   onSilentChange: EventEmitter<HealthSilentChangeData> = new EventEmitter<HealthSilentChangeData>();

   @Output()
   onHealthTestNavigation: EventEmitter<HealthTestNavParams> = new EventEmitter<HealthTestNavParams>();

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   alertMessage: string;

   clusterRef: ManagedObject = ManagedObject.contextObject;

   @Input()
   isVirtualObjectsLinkShown: boolean;

   @Input()
   isDiskSpaceActionShown: boolean = true;

   @Input()
   hasEditPermissions: boolean = false;

   @Input()
   showTestName: boolean = true;

   @Input()
   isSilentCheck: boolean = false;

   @Input()
   isOnlineTest: boolean = false;

   @Input()
   silentCheckSupported: boolean = false;

   @Input()
   isVsphereHealth: boolean;

   @Input()
   isHistoricalTestData: boolean;

   @Input()
   preselectTimestamp: number;
   loadingTestInstances: boolean;
   loadingDetails: boolean;

   @Input()
   showHealthCorrelationMessages: boolean;

   clusterName: string;

   hasLoadTestError: boolean;
   hasLoadInstanceDetailsError: boolean;

   private hasStoragePermissions: boolean = false;

   private isPurgeInaccessibleVmSwapObjectsSupported: boolean;
   private isCapacityCustomizableThresholdsSupported: boolean;

   constructor(private healthMutationProvider: VsanHealthMutationProvider,
         private healthProvider: VsanHealthPropertyProvider,
         private taskSvc: TaskMonitorService,
         private capabilityProvider: VsanCapabilityProvider,
         private permissionService: PermissionService,
         private navigationService: NavigationService,
         private telemetryService: TelemetryService,
         private dataService: DataServiceClient,
         private refWatcher: ReferenceWatcher) {
   }

   async ngOnInit(): Promise<void> {
      this.loadDataHealthCapabilityAndPermissions();
      this.clusterName = await this.dataService.getProperty(ClusterComputeResource.name, ManagedObject.contextObject);
   }

   private loadDataHealthCapabilityAndPermissions(): void {
      // We only check IsPurgeInaccessibleVmSwapObjectsSupported at cluster level health check
      // because this action only exists at cluster level.
      if (ManagedObjectUtils.isCluster(ManagedObject.uid(this.clusterRef))) {
         Promise.all([
            this.capabilityProvider.getIsPurgeInaccessibleVmSwapObjectsSupported(this.clusterRef),
            this.capabilityProvider.getIsCapacityCustomizableThresholdsSupported(this.clusterRef),
         ]).then((results: [boolean, boolean]) => {
            this.isPurgeInaccessibleVmSwapObjectsSupported = results[0];
            this.isCapacityCustomizableThresholdsSupported = results[1];
         }).catch((err) => {
            this.alertMessage = err;
            this.isPurgeInaccessibleVmSwapObjectsSupported = false;
            this.isCapacityCustomizableThresholdsSupported = false;
         });
      }
      this.permissionService.hasPermissions(this.clusterRef, [Privileges.CONFIG_STORAGE()])
            .then(result => this.hasStoragePermissions = result)
            .catch((err) => {
               this.alertMessage = err;
               this.hasStoragePermissions = false;
            });
   }

   isActionSupported = (actionId: string): boolean => {
      return RemediateActions[actionId] !== undefined;
   };

   private _data: VsanTestData;

   @Input()
   set data(value: VsanTestData) {
      this.loadingTestInstances = true;
      this.selectedInstance = null;
      this.hasLoadTestError = false;
      this._data = value;
      if (value) {
         HealthLazyDataLoadingUtil.load(this._data)
               .then(() => {
                  this.resetTabFocus();
                  if (!this.isHistoricalTestData) {
                     this.initializeSelectedInstanceDetails();
                  }
                  this.loadingTestInstances = false;
               })
               .catch(error => {
                  Logger.error(error);
                  this._data.instances = [];
                  this.hasLoadTestError = true;
               })
               .finally(() => this.loadingTestInstances = false);
      }
   }

   get data(): VsanTestData {
      return this._data;
   }

   selectedInstance: VsanTestInstanceDetails;

   resetTabFocus(): void {
      if (this.tabs != null && this.tabs.length > 0) {
         // Reset initial state of first focused element due to correct re-initialization of clr-tabs functionality
         this.tabs.first.el.nativeElement.click();
         // Move focus to the first tab
         this.tabs.first.el.nativeElement.focus();
      }
   }

   getTabTitle = (table: VsanTestTable): string => {
      return !!table.title ? table.title : VsanUiUtils.getString("vsan.monitor.health.result");
   };

   gotoAskMore(): void {
      this.telemetryService.logAction(EventCategory.VSAN_HEALTH,
            this.GOTO_HELP_EVENT,
            this.EVENT_PARAM_NAME,
            this.data.helpId);
      this.navigationService.askVMware(this.data && this.data.helpId);
   }

   navigateToServicesPage = () => {
      const context: ServicesContext = {advancedOptionsExpanded: true};
      this.navigationService.navigateTo(PlatformExtension.CLUSTER_SERVICES, null, context);
   };

   get testMessages(): any[] {
      return this.isHistoricalTestData ? this.selectedInstance?.messages : this.data?.messages;
   }

   openCapacityManagementDialog(): void {
      const context: CapacityManagementContext = {
         clusterName: this.clusterName,
         // Having the old disk version/old format objects means that
         // the vSAN cluster is currently not in a very usable state which is not relevant in this case
         showOldFormatInfo: false,
      };
      new ModalBuilder(VsanRoutes.CAPACITY_MANAGEMENT)
            .setSize(VsanDialogUtil.CAPACITY_MANAGEMENT_DIALOG.width, VsanDialogUtil.CAPACITY_MANAGEMENT_DIALOG.height)
            .open(context)
            .then((taskRef: ManagedObject) => this.refreshWhenTaskDone(taskRef));
   }

   navigateToVumClusterSettings = () => {
      this.navigationService.navigateTo(PlatformExtension.CLUSTER_UPDATES, this.clusterRef);
   };

   navigateToHealthTest(testId: string): void {
      const params: HealthTestNavParams = new HealthTestNavParams(testId);
      if (this.isHistoricalTestData && this.selectedInstance) {
         params.timestamp = this.selectedInstance.timestamp;
      }
      this.onHealthTestNavigation.emit(params);
   }

   isActionDisabled = (action?: HealthAction): boolean => {
      if (!this.hasEditPermissions) {
         return true;
      }
      return action ? !action.enabled : false;
   };

   actionTitle = (action?: HealthAction): string => {
      if (!this.hasEditPermissions) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return action ? action.description : "";
   };

   displayDataDrivenActions(isInfoTab: boolean): boolean {
      const hasSupportedActions: boolean = this.data && this.data.actions && this.data.actions.length > 0 &&
            this.data.actions.some(action => {
               return this.isActionSupported(action.actionId);
            });
      return hasSupportedActions && this.areTestActionsVisibleInTab(isInfoTab);
   }

   displayInfoTab(isInfoTab: boolean): boolean {
      return isInfoTab && this.data.testId !== this.TEST_ID_SKYLINE;
   }

   displayHclDbUpToDateAction(isInfoTab: boolean): boolean {
      const isHclDbUpToDateAction: boolean = this.data.helpId === this.HELP_ID_HCL_UPTODATE;
      return isHclDbUpToDateAction && this.areTestActionsVisibleInTab(isInfoTab);
   }

   displayReleaseCatalogUpToDateAction(isInfoTab: boolean): boolean {
      const isReleaseCatalogUpToDateAction: boolean = this.data.helpId === this.HELP_ID_RELEASE_CATALOG_UPTODATE;
      return isReleaseCatalogUpToDateAction && this.areTestActionsVisibleInTab(isInfoTab);
   }

   displayDiskBalanceAction(isInfoTab: boolean): boolean {
      const isDiskBalanceAction: boolean = this.data.helpId === this.HELP_ID_DISK_BALANCE;
      return isDiskBalanceAction && this.areTestActionsVisibleInTab(isInfoTab);
   }

   displayDiskSpaceAction(isInfoTab: boolean): boolean {
      const isDiskSpaceAction: boolean = this.data.helpId === this.HELP_ID_DISK_SPACE;
      return isDiskSpaceAction &&
            this.isDiskSpaceActionShown &&
            this.areTestActionsVisibleInTab(isInfoTab) &&
            this.isCapacityCustomizableThresholdsSupported;
   }

   displayDeviceModelSelectAction(isInfoTab: boolean): boolean {
      const isDeviceModelSelectAction: boolean = this.data.helpId === this.HELP_ID_SELECT_VCG_DEVICE_MODELS;
      return isDeviceModelSelectAction && this.areTestActionsVisibleInTab(isInfoTab);
   }

   private areTestActionsVisibleInTab(isInfoTab: boolean): boolean {
      // is not info tab OR is info tab without details
      return !this.isHistoricalTestData &&
            (!isInfoTab || (isInfoTab && !this.selectedInstance.details));
   }

   isDataDrivenActionDisabled = (action: HealthAction): boolean => {
      if (this.isPurgeInaccessibleVmSwapObjectsAction(action.actionId)) {
         return !action.enabled || !this.hasEditPermissions || !this.hasStoragePermissions;
      }
      return !action.enabled || !this.hasEditPermissions;
   };

   private isPurgeInaccessibleVmSwapObjectsAction = (actionId: string): boolean => {
      return RemediateActions[actionId] === RemediateActions.PurgeInaccessSwapObjs;
   };

   /**
    * The action may 1: open a dialog 2: send request directly 3: navigate to another page
    * For #1&2 we will use eventemitter in the callback so parent component can take further
    * actions(refresh the page, reload some data, etc)
    */
   remediateBtnClick = (actionId: string): void => {
      switch (RemediateActions[actionId]) {
         case RemediateActions.RemediateDedup:
            const params: RemediateClusterConfigContextData = {
               isDedupAction: true,
               title: VsanUiUtils.getString("vsan.monitor.health.disk.format.remediate.title"),
            };
            new ModalBuilder("healthActions/remediateCluster")
                  .setSize(700, 300)
                  .open(params)
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.RemediateFileService:
            new ModalBuilder("healthActions/remediateCluster")
                  .setSize(700, 350)
                  .open({
                     title: VsanUiUtils.getString("vsan.monitor.health.remediate.fileservice.config.dlg.title"),
                  } as RemediateClusterConfigContextData)
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.RemediateClusterConfig:
            new ModalBuilder("healthActions/remediateCluster")
                  .setSize(700, 350)
                  .open({
                     title: VsanUiUtils.getString("vsan.monitor.health.remediate.cluster.config.dlg.title"),
                  } as RemediateClusterConfigContextData)
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.RepairClusterObjectsAction:
            this.healthMutationProvider.repairClusterObjectsImmediate(this.clusterRef)
                  .then(this.refreshWhenTaskDone)
                  .catch(() => {
                     this.alertMessage = VsanUiUtils.getString("vsan.monitor.cluster.repair.error");
                  });
            break;
         case RemediateActions.PurgeInaccessSwapObjs:
            if (this.isPurgeInaccessibleVmSwapObjectsSupported) {
               new ModalBuilder("healthActions/purgeInaccessibleSwapObjs")
                     .setSize(550, 220)
                     .open()
                     .then(this.refreshWhenTaskDone);
            } else {
               new ModalBuilder("healthActions/notSupportedAction")
                     .setSize(550, 180)
                     .open();
            }
            break;
         case RemediateActions.RelayoutVsanObjects:
            new ModalBuilder("healthActions/relayoutVsanObjects")
                  .setSize(600, 250)
                  .open()
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.UpgradeVsanDiskFormat:
            new ModalBuilder("upgrade/upgrade")
                  .setSize(700, 350)
                  .open()
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.LoginVumIsoDepot:
            new ModalBuilder("healthActions/loginVumDepot")
                  .setSize(550, 290)
                  .open();
            break;
         case RemediateActions.EnablePerformanceServiceAction:
            new ModalBuilder(VsanRoutes.PERFORMANCE_CONFIG)
                  .setSize(VsanDialogUtil.PERF_SVC_EDIT_ACTION_DIALOG.width,
                        VsanDialogUtil.PERF_SVC_EDIT_ACTION_DIALOG.height)
                  .open({
                     clusterRef: this.clusterRef,
                  })
                  .then(this.refreshWhenTaskDone);
            break;
         case RemediateActions.EnableCeip:
            this.navigationService.navigateWithoutContextObject(PlatformExtension.CEIP_VIEW);
            break;
         case RemediateActions.ClusterUpgrade:
            this.navigateToVumClusterSettings();
            break;
         case RemediateActions.RemediateFileServiceImbalance:
            new ModalBuilder("healthActions/remediateFileServiceImbalance")
                  .setSize(700, 400)
                  .open({
                     clusterRef: this.clusterRef,
                  })
                  .then(this.refreshWhenTaskDone);
            break;
         default:
            Logger.warn("Unknown actionId: " + actionId);
      }
   };

   instanceSelected(instanceTimestamp: number): void {
      this.initializeSelectedInstanceDetails(instanceTimestamp);
   }

   private initializeSelectedInstanceDetails(instanceTimestamp?: number): void {
      this.hasLoadInstanceDetailsError = false;
      if (!this.data || !this.data.instances || !this.data.instances.length) {
         this.selectedInstance = null;
         return;
      }

      this.loadingDetails = true;

      const result: VsanTestInstanceDetails = instanceTimestamp ?
            this.data.instances.find(instance => instance.timestamp === instanceTimestamp) :
            null;
      if (result || this.data.instances.length) {
         // do not force to select latest instance in historical mode. The historical-test-details component
         // will do this if needed
         this.selectedInstance = result || this.isHistoricalTestData ?
               result : this.data.instances[this.data.instances.length - 1];
      }
      HealthLazyDataLoadingUtil.load(this.selectedInstance)
            .then(() => this.refWatcher.setTimeout(() => {
               this.resetTabFocus();
            }, 0))
            .catch(e => {
               Logger.error(e);
               this.selectedInstance.details = [];
               this.hasLoadInstanceDetailsError = true;
            })
            .finally(() => this.loadingDetails = false);
   }

   private refreshWhenTaskDone = (task: ManagedObject): void => {
      if (!task) {
         return;
      }
      this.taskSvc.getAwaitResult(task).then(() => this.triggerRetestWithoutOnlineHealth());
   };

   isSilnceCheckAvailable(): boolean {
      return !this.isHistoricalTestData && this.silentCheckSupported && this.data &&
            this.data.testId !== this.TEST_ID_SKYLINE;
   }

   switchSilentCheck(): void {
      if (this.isSilentCheck) {
         this.restoreAlert();
      } else {
         this.silenceAlert();
      }
   }

   silenceAlert(): void {
      const context: SilentHealthActionContext = {
         testId: this.data.silenceTestId,
         isVsphereHealth: this.isVsphereHealth,
      };
      new ModalBuilder("healthActions/markSilent")
            .setSize(550, 300)
            .open(context)
            .then((silent: boolean): void => {
               if (silent) {
                  this.isSilentCheck = silent;
                  this.onSilentChange.emit(new HealthSilentChangeData(true));
               }
            });
   }

   restoreAlert(): void {
      const context: RestoreHealthActionContext = {
         testId: this.data.testId,
         isOnlineTest: this.isOnlineTest,
      };
      new ModalBuilder("healthActions/restoreAlert")
            .setSize(VsanDialogUtil.HEALTH_RESTORE_ACTION_DIALOG.width,
                  VsanDialogUtil.HEALTH_RESTORE_ACTION_DIALOG.height)
            .open(context)
            .then((settings: RestoreHealthSettings): void => {
               this.healthProvider.setSilentChecks(this.clusterRef, this.isVsphereHealth, [],
                     [this.data.silenceTestId])
                     .then(() => {
                        this.isSilentCheck = false;
                        this.onSilentChange.emit(new HealthSilentChangeData(false, settings));
                     })
                     .catch(() => {
                        this.onError.emit(VsanUiUtils.getString("vsan.monitor.health.silent.error"));
                     });
            });
   }

   getSilenceRestoreAlertTitle(): string {
      return this.isSilentCheck ?
            VsanUiUtils.getString("vsan.monitor.health.silent.restore.action.label") :
            VsanUiUtils.getString("vsan.monitor.health.silent.action.label");
   }

   setErrorMessage(error: string): void {
      this.alertMessage = error;
   }

   isTestNameShown(): boolean {
      return this.showTestName && this.data && (!!this.data.testName || !!this.data.testDescription);
   }

   displayVirtualObjectNavigationLink(): boolean {
      return this.isVirtualObjectsLinkShown && !this.isHistoricalTestData;
   }

   triggerRetestWithoutOnlineHealth(): void {
      this.onRetest.emit(new HealthRetestEventData(false));
   }
}
