import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanTestInstanceDetails} from "@generated/vsan-test-instance-details";
import {BasePeriod} from "./periods/base-period";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {WeekPeriod} from "./periods/week-period";
import {DayPeriod} from "./periods/day-period";
import {LargestPeriod} from "./periods/largest-period";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {ShowHideState} from "@directive/show-hide/show-hide.directive";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthOverviewData} from "@generated/vsan-health-overview-data";

@Component({
   templateUrl: "historical-test-details.html",
   styleUrls: ["historical-test-details.scss"],
   selector: "vsan-historical-test-details",
})
export class HistoricalTestDetailsComponent {

   private readonly GROUPING_ELEMENTS_COUNT: number = 4;
   // display the periods if are at least this count. Otherwise try to drill up / down. The reason is we don't want
   // to have grouping of 1 period.
   private readonly MINIMUM_PERIODS_TO_DISPLAY: number = 2;
   private readonly FEW_INSTANCES_COUNT_THRESHOLD: number = 3;

   // these statuses may be in each group - green, yellow, red
   private readonly NEUTRAL_HEALTH_STATUSES: VsanHealthStatus[] = [VsanHealthStatus.unknown, VsanHealthStatus.info];

   rootPeriod: BasePeriod;
   private _instances: VsanTestInstanceDetails[];
   selectedInstance: VsanTestInstanceDetails;

   private _selectedPeriod: BasePeriod;

   set selectedPeriod(value: BasePeriod) {
      this._selectedPeriod = value;
      if (!this._selectedPeriod || !this._selectedPeriod.testInstances) {
         this.instancesByStatus = [];
         return;
      }
      this.instancesByStatus = this.getInstancesByStatus(this._selectedPeriod.testInstances);
   }

   get selectedPeriod(): BasePeriod {
      return this._selectedPeriod;
   }

   instancesByStatus: VsanTestInstancesGroup[] = [];

   @Input()
   overviewData: VsanHealthOverviewData;

   @Input()
   selectedInstanceTimestamp: number;

   @Input()
   isOverviewCorrelationsDisplayed: boolean = false;

   @Input()
   set instances(values: VsanTestInstanceDetails[]) {
      this._instances = values;
      this.rootPeriod = null;
      if (!VsanUiUtils.isEmpty(values)) {
         this.rootPeriod = this.calculateRootPeriod(values[0].timestamp, values[values.length - 1].timestamp);
         values.forEach(instance => this.rootPeriod.propagateTestInstance(instance));
      }

      if (this.selectedInstanceTimestamp) {
         this.preselectInstance(this.selectedInstanceTimestamp);
      } else {
         this.selectedPeriod = this.getPeriodToDrillDown(this.rootPeriod);
         this.selectPeriodLatestInstance(this.selectedPeriod);
      }
   }

   private preselectInstance(timestamp: number): void {
      if (!this.rootPeriod) {
         return;
      }

      if (VsanUiUtils.isEmpty(this.rootPeriod.childPeriods)) {
         this.selectedPeriod = this.getPeriodToDrillDown(this.rootPeriod);
         this.selectInstance(this.selectedInstanceTimestamp);
         this.expandPreselectedInstanceGroup(timestamp);
         return;
      }

      const periods: BasePeriod[] = [].concat(...this.rootPeriod.childPeriods);
      while (periods.length > 0) {
         const current: BasePeriod = periods.pop();
         if (!(current instanceof DayPeriod)) {
            periods.concat(...current.childPeriods);
            continue;
         }
         if (current.hasTestInstance(timestamp)) {
            this.selectedPeriod = current;
            this.expandPreselectedInstanceGroup(timestamp);
            this.selectInstance(timestamp);
            break;
         }
      }
   }

   private expandPreselectedInstanceGroup(timestamp: number): void {
      const instancesGroup: VsanTestInstancesGroup = this.instancesByStatus.find(instance =>
            instance?.instances?.find(details => details?.timestamp === timestamp));
      if (instancesGroup) {
         instancesGroup.defaultExpandableGroupState = ShowHideState.SHOWN;
      }
   }

   @Output()
   onInstanceSelect: EventEmitter<number> = new EventEmitter<number>();

   getOverviewLabel(selectedInstance: VsanTestInstanceDetails): string {
      const instanceDate: string = VsanHealthUtil.formatInstanceTimestamp(selectedInstance?.timestamp);
      if (this.isOverviewCorrelationsDisplayed) {
         return VsanUiUtils.getString("vsan.health.historical.details.correlations.overview", instanceDate);
      }

      return VsanUiUtils.getString("vsan.health.historical.details.instance.overview", instanceDate,
            VsanHealthUtil.getVsanHealthStatusText(selectedInstance?.status));
   }

   private calculateRootPeriod(firstTimestamp: number, lastTimestamp: number): BasePeriod {
      // root depends on timestamp of included instances
      // 1. if instances are for more than 7 days, the grouping should be by weeks. Root of week is LargestPeriod.
      if ((lastTimestamp - firstTimestamp) / VsanDateTimeUtils.DAY_IN_MILLISECONDS >= VsanDateTimeUtils.DAYS_IN_WEEK) {
         return new LargestPeriod();
      }
      // 2. if instances are for more 24 hours, the grouping should be by days. Root is WeekPeriod.
      if ((lastTimestamp - firstTimestamp) / VsanDateTimeUtils.HOUR_IN_MILLISECONDS > VsanDateTimeUtils.HOURS_IN_DAY) {
         return new WeekPeriod();
      }
      // 3. if instances are for less than 24 hours, there should be no grouping. Root is DayPeriod.
      return new DayPeriod();
   }

   getPeriodToDrillDown(period: BasePeriod): BasePeriod {
      if (!period) {
         return null;
      }
      if (!VsanUiUtils.isEmpty(period.testInstances)) {
         return period;
      }
      if (VsanUiUtils.isEmpty(period.childPeriods)) {
         return null;
      }
      if (period.childPeriods.length >= this.MINIMUM_PERIODS_TO_DISPLAY) {
         return period;
      }
      return this.getPeriodToDrillDown(period.childPeriods[0]);
   }

   getPeriodToDrillUp(period: BasePeriod): BasePeriod {
      const parentPeriod: BasePeriod = period.parentPeriod;
      if (!parentPeriod) {
         return null;
      }

      if (parentPeriod.childPeriods.length >= this.MINIMUM_PERIODS_TO_DISPLAY) {
         return parentPeriod;
      }
      return this.getPeriodToDrillUp(parentPeriod);
   }

   drillDown(period: BasePeriod): void {
      this.selectedPeriod = this.getPeriodToDrillDown(period);
      this.instancesByStatus = this.getInstancesByStatus(this.selectedPeriod.testInstances);
      this.selectPeriodLatestInstance(this.selectedPeriod);
   }

   drillUp(): void {
      const parentPeriod: BasePeriod = this.getPeriodToDrillUp(this.selectedPeriod);
      if (parentPeriod) {
         this.selectedPeriod = parentPeriod;
         this.selectPeriodLatestInstance(this.selectedPeriod);
      }
   }

   private getInstancesByStatus(instances: VsanTestInstanceDetails[]): VsanTestInstancesGroup[] {
      const result: VsanTestInstancesGroup[] = [];
      if (VsanUiUtils.isEmpty(instances)) {
         return result;
      }

      // add first empty group
      result.push(new VsanTestInstancesGroup());

      const instancesLeft: VsanTestInstanceDetails[] = instances.slice(0, instances.length);
      while (instancesLeft.length > 0) {
         const previousGroup: VsanTestInstancesGroup = result[result.length - 1];
         const count: number = this.findCountOfFirstWithSameStatus(instancesLeft);
         if (count < this.GROUPING_ELEMENTS_COUNT) {
            // just add instances to the previous group
            previousGroup.instances = previousGroup.instances.concat(instancesLeft.splice(0, count));
            continue;
         }
         // should have expandable group:
         // 1. add first of instances to the previous group (start wrapper of the expandable group)
         previousGroup.instances.push(instancesLeft.splice(0, 1)[0]);
         // 2. create new group with all but first and last instance (expandable group)
         result.push(new VsanTestInstancesGroup(instancesLeft.splice(0, count - 2), true));
         // 3. create new group with the last element (end wrapper of the expandable group)
         result.push(new VsanTestInstancesGroup(instancesLeft.splice(0, 1)));
      }

      return result;
   }

   private findCountOfFirstWithSameStatus(instances: VsanTestInstanceDetails[]): number {
      if (VsanUiUtils.isEmpty(instances)) {
         return 0;
      }

      const status: VsanHealthStatus = instances[0].status;
      for (let i: number = 1; i < instances.length; i++) {
         if (status !== instances[i].status) {
            return i;
         }
      }
      // all instances are with same status
      return instances.length;
   }

   selectInstance(timestamp: number): void {
      this.selectedInstance = this.findInstanceByTimestamp(timestamp);
      this.onInstanceSelect.emit(this.selectedInstance ? this.selectedInstance.timestamp : null);
   }

   private selectPeriodLatestInstance(period: BasePeriod): void {
      if (!period || VsanUiUtils.isEmpty(this.selectedPeriod.testInstances)) {
         this.selectInstance(null);
         return;
      }
      const latestPeriodInstance: VsanTestInstanceDetails = period.testInstances[period.testInstances.length - 1];
      this.selectInstance(latestPeriodInstance.timestamp);
   }

   private findInstanceByTimestamp(timestamp: number): VsanTestInstanceDetails {
      if (!timestamp || !this.selectedPeriod || !this.selectedPeriod.testInstances) {
         return null;
      }
      return this.selectedPeriod.testInstances.find(instance => instance.timestamp === timestamp);
   }

   isInstanceTitleVisible(timespamp: number): boolean {
      if (VsanUiUtils.isEmpty(this.selectedPeriod.testInstances)) {
         return false;
      }
      // show for fist and last instance
      if (timespamp === this.selectedPeriod.testInstances[0].timestamp) {
         return true;
      }
      if (timespamp === this.selectedPeriod.testInstances[this.selectedPeriod.testInstances.length - 1].timestamp) {
         return true;
      }
      return false;
   }

   getGroupState(group: VsanTestInstancesGroup): ShowHideState {
      if (!this.hasStatusSwitches()) {
         return ShowHideState.DISABLED;
      }
      return group.isExpandable ? group.defaultExpandableGroupState : ShowHideState.DISABLED;
   }

   private hasStatusSwitches(): boolean {
      if (!this.selectedPeriod || VsanUiUtils.isEmpty(this.selectedPeriod.testInstances)) {
         return false;
      }
      const firstInstanceStatus: VsanHealthStatus = this.selectedPeriod.testInstances[0].status;
      return this.selectedPeriod.testInstances.some(instance => instance.status !== firstInstanceStatus);
   }

   getGroupStatus(group: VsanTestInstancesGroup): VsanHealthStatus {
      if (!group || VsanUiUtils.isEmpty(group.instances)) {
         return null;
      }
      return group.instances[0].status;
   }

   getGroupPeriod(group: VsanTestInstancesGroup): string {
      if (!group || VsanUiUtils.isEmpty(group.instances)) {
         return null;
      }
      return `${group.instances[0].timestamp}-${group.instances[group.instances.length - 1].timestamp}`;
   }

   hasFewInstances(): boolean {
      return this.selectedPeriod.propagatedInstancesCount <= this.FEW_INSTANCES_COUNT_THRESHOLD;
   }
}

export class VsanTestInstancesGroup {
   constructor(public instances: VsanTestInstanceDetails[] = [], public isExpandable: boolean = false,
         public defaultExpandableGroupState: ShowHideState = ShowHideState.HIDDEN) {
   }
}