import {Component, EventEmitter, Input, Output} from "@angular/core";
import {DuplicatePciIdUpdateDialogContext} from "@navigation/context/duplicate-pci-id-update-context";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../../vsan-routes";

@Component({
   templateUrl: "duplicate-pci-id-update-action.html",
   selector: "vsan-duplicate-pci-id-update-action",
   styleUrls: ["update-catalog-action.scss"],
})
export class DuplicatePciIdUpdateActionComponent {
   @Input()
   clusterName: string;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   isDisabled: boolean;

   @Input()
   hasEditPermission: boolean;

   @Output()
   onModelSelected: EventEmitter<void> = new EventEmitter<void>();

   get title(): string {
      return !this.hasEditPermission
            ? VsanUiUtils.getString("vsan.common.nopermissions")
            : VsanUiUtils.getString("vsan.cluster.duplicatePciIdDevices.remediateAction.label");
   }

   openSelectDialog(): void {
      const context: DuplicatePciIdUpdateDialogContext = {
         clusterName: this.clusterName,
         clusterRef: this.clusterRef,
         isOpenedFromHealthCheck: true,
      };
      new ModalBuilder(`${VsanRoutes.UPDATE_VIEW}/updateDuplicatePciIdDevices`)
            .setSize(VsanDialogUtil.DUPLICATE_PCI_ID_MODEL_SELECTION_DIALOG.width,
                  VsanDialogUtil.DUPLICATE_PCI_ID_MODEL_SELECTION_DIALOG.height)
            .open(context)
            .then((isConfigChanged: boolean) => {
               if (isConfigChanged) {
                  this.onModelSelected.emit();
               }
            });
   }
}