import {Component, EventEmitter, Input, Output} from "@angular/core";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {VsanBasicTestData} from "@generated/vsan-basic-test-data";
import {HealthTestMessage} from "@generated/health-test-message";
import {CausedIssueHealthTestMessage} from "@generated/caused-issue-health-test-message";
import {CoreIssueHealthTestMessage} from "@generated/core-issue-health-test-message";
import {HealthMessageType} from "@generated/health-message-type";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "health-messages.html",
   styleUrls: ["health-messages.scss"],
   selector: "vsan-health-messages",
})
export class HealthMessagesComponent {
   @Input()
   messages: HealthTestMessage[];

   @Input()
   showHealthCorrelationMessages: boolean;

   @Output()
   onTestNavigation: EventEmitter<string> = new EventEmitter<string>();

   get basicMessages(): HealthTestMessage[] {
      return this.messages
            .filter(m => !this.isCausedIssueBannerMessage(m) && !this.isCoreIssueBannerMessage(m));
   }

   get causedIssueMessages(): CausedIssueHealthTestMessage[] {
      return this.messages.filter(m =>
               this.verifyMessage(m) &&
               this.isCausedIssueBannerMessage(m) &&
               this.hasTestData(m.coreIssueTest))
            .map(m => m as CausedIssueHealthTestMessage);
   }

   get coreIssueMessages(): CoreIssueHealthTestMessage[] {
      return this.messages.filter(m =>
               this.verifyMessage(m) &&
               this.isCoreIssueBannerMessage(m) &&
               !VsanUiUtils.isEmpty(m?.causedIssuesTests) &&
               !m.causedIssuesTests.some(t => !this.hasTestData(t)))
            .map(m => m as CoreIssueHealthTestMessage);
   }

   private verifyMessage(message: HealthTestMessage): boolean {
      return !!message?.alertType && !!message?.message;
   }

   private hasTestData(basicTestData: VsanBasicTestData): boolean {
      return !!basicTestData?.testId && !!basicTestData?.testName;
   }

   getAlertType(messageType: HealthMessageType): string {
      switch (messageType) {
         case HealthMessageType.INFO:
            return "info";
         case HealthMessageType.WARNING:
            return "warning";
         default:
            return "";
      }
   }

   getActionLinks(healthMessage: CoreIssueHealthTestMessage | CausedIssueHealthTestMessage): AlertActionLink[] {
      const links: AlertActionLink[] = [];
      if (this.isCausedIssueBannerMessage(healthMessage)) {
         const banner: CausedIssueHealthTestMessage = healthMessage as unknown as CausedIssueHealthTestMessage;
         const link: AlertActionLink = {
            label: banner.coreIssueTest.testName,
            action: () => this.onTestNavigation.emit(banner.coreIssueTest.testId),
         };
         links.push(link);
      } else if (this.isCoreIssueBannerMessage(healthMessage)) {
         const banner: CoreIssueHealthTestMessage = healthMessage as unknown as CoreIssueHealthTestMessage;
         banner.causedIssuesTests.forEach((causedIssuesTest: VsanBasicTestData) => {
            const link: AlertActionLink = {
               label: causedIssuesTest.testName,
               action: () => this.onTestNavigation.emit(causedIssuesTest.testId),
            };
            links.push(link);
         });
      }
      return links;
   }

   isCausedIssueBannerMessage(healthMessage: any): healthMessage is CausedIssueHealthTestMessage {
      return (healthMessage as CausedIssueHealthTestMessage).coreIssueTest != null;
   }

   isCoreIssueBannerMessage(healthMessage: any): healthMessage is CoreIssueHealthTestMessage {
      return (healthMessage as CoreIssueHealthTestMessage).causedIssuesTests != null;
   }
}
